import { mapGetters } from 'vuex';

export default {
  name: 'WebcamsPage',
  data() {
    return {
      interval: 0,
    };
  },
  mounted() {
    // This interval increments the query param every 60 seconds causing an image refresh
    setInterval(() => {
      this.interval++;
    }, 60000);
  },
  computed: {
    ...mapGetters('config', ['webcamLinks']),
  },
};
